import React, {useState} from 'react';
import './App.css'
import { debugData } from "../utils/debugData";
import { fetchNui } from "../utils/fetchNui";
import { useNuiEvent } from "../hooks/useNuiEvent";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { 
  faSuitcase, 
  faIdCard, 
  faCircleLeft, 
  faCircleXmark, 
  faAddressCard,
  faTrowel,
  faTaxi,
  faTruck,
  faTruckPickup,
  faHeadset,
  faTrashCan,
  faBus,
  faHotdog
} from '@fortawesome/free-solid-svg-icons'

import Button from '@mui/material/Button';
import AllInboxIcon from '@mui/icons-material/AllInbox';

const style = {
  width: '100%',
  maxWidth: 360,
  bgcolor: 'background.paper',
};

// This will set the NUI to visible if we are
// developing in browser
debugData([
  {
    action: 'setVisible',
    data: true,
  }
])

const App: React.FC = () => {
  const [PageIndex, setPageIndex] = useState(0);
  const [JobsData, setJobsData] = useState<any | null>([]);
  const [LicData, setLicData] = useState<any | null>([]);

  const GoToMain = () => {
    setPageIndex(0);
  };
  const GoToJobs = () => {
    setPageIndex(1);
  };
  const GoToIden = () => {
    setPageIndex(2);
  };
  const Close = () => {
    fetchNui('hideFrame')
  };
  useNuiEvent('setJobs', (data: any) => {
    setPageIndex(0);
    let jobs = data.jobs
    const entries = Object.entries(jobs);
    setJobsData(entries)
    fetchNui('requestLicenses').then(licenses => {
      const entries2 = Object.entries(licenses);
      setLicData(entries2)
    })
  })
  const ApplyToJob = (job: any) => {
    fetchNui('applyJob', {selectedJobId: job})
  };
  const BuyIden = (selectedIdentityType: any, selectedIdentityCost: any) => {
    fetchNui('requestId', {type: selectedIdentityType, cost: selectedIdentityCost})
  };
  return (
    <div className='cityhall-panel'>
      <div className='cityhall-panel__header'>
        {PageIndex === 1 ? 
        <>
          <div className='button-back'><FontAwesomeIcon icon={faCircleLeft} onClick={GoToMain} /></div>
          <div className='cityhall-panel__title'>Los Santos City Hall</div>
          <div className='button-close'><FontAwesomeIcon icon={faCircleXmark} onClick={Close} /></div>
        </>
        :PageIndex === 2 ?
        <>
          <div className='button-back'><FontAwesomeIcon icon={faCircleLeft} onClick={GoToMain} /></div> 
          <div className='cityhall-panel__title'>Los Santos City Hall</div>
          <div className='button-close'><FontAwesomeIcon icon={faCircleXmark} onClick={Close} /></div>
        </>
        :
        <>
          <div className='button-back-hidden'><FontAwesomeIcon icon={faCircleLeft} /></div> 
          <div className='cityhall-panel__title'>Los Santos City Hall</div>
          <div className='button-close'><FontAwesomeIcon icon={faCircleXmark} onClick={Close} /></div>
        </>
        }
      </div>
      {PageIndex && PageIndex === 1 ?
        <section className="main-section">
          {
            JobsData.map((data: any) =>
              <div className="card">
                <div className='card-header'>
                    <div className='card-icon'>
                      {
                        data[1].icon === 1 ? 
                          <FontAwesomeIcon icon={faTruck} />
                        :data[1].icon === 2 ? 
                          <FontAwesomeIcon icon={faTaxi} />
                        :data[1].icon === 3 ? 
                          <FontAwesomeIcon icon={faTruckPickup} />
                        :data[1].icon === 4 ? 
                          <FontAwesomeIcon icon={faHeadset} />
                        :data[1].icon === 5 ? 
                          <FontAwesomeIcon icon={faTrashCan} />
                        :data[1].icon === 6 ? 
                          <FontAwesomeIcon icon={faBus} />
                        :data[1].icon === 7 ? 
                          <FontAwesomeIcon icon={faHotdog} />
                        :
                          <FontAwesomeIcon icon={faSuitcase} />
                      }
                    </div>
                    <div className='card-tag'>${data[1].salary}/h</div>
                </div>
                <div className='card-title'>{data[1].label}</div>
                <div className='card-desc'>{data[1].desc}</div>
                <div className='card-button'><Button variant="contained" onClick={() => ApplyToJob(data[0])}>Apply</Button></div>
              </div>
            )
          }
        </section>
      :PageIndex === 2 ?
        <section className="main-section">
          {
            LicData.map((data: any) =>
              <div className="card">
                <div className='card-header2'>
                    <div className='card-icon'>
                      <FontAwesomeIcon icon={faAddressCard} />
                    </div>
                    <div className='card-tag'>${data[1].cost}</div>
                </div>
                <div className='card-title'>{data[1].label}</div>
                <div className='card-desc'>{data[1].desc}</div>
                <div className='card-button'><Button variant="contained" onClick={() => BuyIden(data[0], data[1].cost)}>Buy</Button></div>
              </div>
            )
          }
        </section>
      :
        <section className="main-section2">
          <div className="card2" onClick={GoToIden}>
          <div className='card-icon2'><FontAwesomeIcon icon={faIdCard} /></div>
            <div className='card-title2'>IDENTITY</div>
          </div>
          <div className="card2" onClick={GoToJobs}>
            <div className='card-icon2'><FontAwesomeIcon icon={faSuitcase} /></div>
            <div className='card-title2'>EMPLOYMENT</div>
          </div>
        </section>
      }
    </div>
  );
}

export default App;
